import React from 'react';
import ReactDOM from 'react-dom';
import './css/index.css';

const recentLink = 'https://fcctop100.herokuapp.com/api/fccusers/top/recent';
const alltimeLink = 'https://fcctop100.herokuapp.com/api/fccusers/top/alltime';


class Table extends React.Component {
	constructor(props){
		super(props);
		this.isActive = this.isActive.bind(this);
	}
  
  //Checking which filter is active
  isActive(value){
  	return value === this.props.sort ? 'arrow' : '';
  }

  render() {
    
    const recentUsers = this.props.recentItems;
    const alltimeUsers = this.props.alltimeItems;
    
    const sort = this.props.sort
    console.log(sort);

    const error = this.props.error;
    const isLoaded = this.props.isLoaded;
    let id = this.props.userId;

    if (error) {
      return <div>Error: {error.warning} </div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <table>
          <caption>FCC Leaderboard</caption>
          <thead>
            <tr>
              <th>#</th>
              <th>Camper Name</th>
              <th onClick={event => {this.props.onClick('Recent')}} className={this.isActive('Recent')}>Points in past 30 days</th>
              <th onClick={event => {this.props.onClick('Alltime')}} className={this.isActive('Alltime')}>All time points</th>
            </tr>
          </thead>
          <tbody>
            {sort === 'Recent' &&
              recentUsers.map(user => (
                <tr key={user.username}>
                  <td>{id++}</td>
                  <td>
                  <a href={"https://www.freecodecamp.org/" + user.username} target="_blank">
	                  <img src={user.img} alt="user avatar" className="userLogo" />
	                  	{user.username}
	                  </a>
                  </td>
                  <td>{user.recent}</td>
                  <td>{user.alltime}</td>
                </tr>
                )
              )}
            {sort === 'Alltime' &&
              alltimeUsers.map(user => (
                <tr key={user.username}>
                  <td>{id++}</td>
                  <td>
                  <a href={"https://www.freecodecamp.org/" + user.username} target="_blank">
                  	<img src={user.img} alt="user avatar" className="userLogo" />
                  		{user.username}
                  	</a>
                  </td>
                  <td>{user.recent}</td>
                  <td>{user.alltime}</td>
                </tr>
              )
              )}
          </tbody>
        </table>
      );
    }
  }
}



class TableContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      recentItems: [],
      alltimeItems:[],
      isLoaded: false,
      sort: 'Recent',
      id: 1
    };
    this.handleSort = this.handleSort.bind(this);
  }

  handleSort(sort){
    this.setState({
      sort: sort
    });
  }

  fetchAllData(){
    fetch(recentLink)
      .then(res => {
        return res.json();
      })
      .then(result => {
        this.setState({
          recentItems: result,
          isLoaded: true
        });
      })
      .then(() => {
        fetch(alltimeLink).then(res => {return res.json()}).then(result => {
          this.setState({
            alltimeItems: result,
            isLoaded: true
          })
        })
      })
      .catch(error => {
        this.setState({
          error: error,
          isLoaded: true
        });
      });
  }

  componentDidMount() {
    this.fetchAllData();
  }

  render() {
    return (
      <Table
        onClick={this.handleSort}
        sort={this.state.sort}
        alltimeItems={this.state.alltimeItems}
        recentItems={this.state.recentItems}
        error={this.state.error}
        isLoaded={this.state.isLoaded}
        userId={this.state.id}
      />
    );
  }
}

ReactDOM.render(<TableContainer />, document.getElementById("root"));




